.page-not-found {
    font-family: 'Roboto', sans-serif;
    background-color: #37b08a;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: auto;
    margin-top: 15vh;
    height: 50vh;
    padding: 5vh;
}

.page-not-found h1 {
    font-size: 10vh;
    font-weight: bold;
    color: white;
}

.page-not-found div {
    font-size: 5vh;
    color: white;
}

img {
    width: 350px;
}
