nav div {
    cursor: pointer;
    background-color: rgba(199, 237, 227, 0.9);
    padding: 1vh 1vw;
    margin: 0 1vw;
    border: none;
    font-family: 'Roboto';
}

nav div:hover {
    background-color: rgba(81, 146, 129, 0.9);
    color: white;
}

nav {
    display: flex;
    align-content: center;
    justify-content: flex-end;
    margin: 0 auto;
    width: 95%;
}
.books-table {
    table-layout: fixed;
    width: 97%;
    margin: 0 auto;
    border-collapse: collapse;
    margin-bottom: 4vh;
    font-family: inherit;
    box-sizing: border-box;
}

.add-book-button-description {
    width: 60%;
    margin: 0 auto;
    text-align: center;
    font-size: 1.3rem;
    padding: 2vh 0;
}

.add-book-btn-container {
    padding: 7vh 0;
}

.add-book-btn {
    display: block;
    margin: 0 auto;
    width: 20vw;
    font-size: 1.5rem;
    padding: 1.5vw;
    background-color: #55cfb5;
    color: rgb(168, 127, 127);
    border: none;
}

.books-table th {
    background-color: #080a09;
    color: rgba(173, 245, 230, 0.76);
}

.books-table th,
.books-table td {
    padding: 1vw;
    text-align: left;
    border: 1px solid white;
}

.books-table td {
    vertical-align: top;
}

.books-table td:nth-child(1) {
    font-weight: 600;
    color: black;
}

.books-table td {
    color: #616058;
}

.books-table thead th:nth-child(1),
.books-table thead th:nth-child(2),
.books-table thead th:nth-child(4),
.books-table thead th:nth-child(7),
.books-table thead th:nth-child(5) {
    width: 9%;
}

.books-table thead th:nth-child(3) {
    width: 5%;
}

.books-table thead th:nth-child(6) {
    width: 45%;
}

.done {
    background-color: #86e09d;
}

.in-progress {
    background-color: #fafc99;
}

.planning {
    background-color: #c2c1ba;
}

.books-table button {
    background-color: rgba(19, 19, 19, 0.055);
    border: none;
    margin: 1vh auto;
    display: block;
    cursor: pointer;
}

.books-table button:hover,
.add-book-btn:hover {
    background-color: rgba(160, 153, 153, 0.438);
}
.book-form input,
.book-form textarea {
    display: block;
    width: 100%;
    font-size: 1.2rem;
    font-family: inherit;
    padding: 2.5vh 1vh;
}

.book-form {
    font-size: 1.2rem;
    width: 80%;
    background-color: rgba(154, 219, 203, 0.829);
    padding: 4vh;
    margin: 16vh auto;
    box-shadow: -1px 2px 21px 6px rgba(99, 169, 150, 0.73);
}

.book-form h2 {
    text-align: center;
}

.books-form-questions {
    margin: 0 auto;
    width: 90%;
}

.error-msg-book-form {
    color: orangered;
    padding-bottom: 2vh;
}

.book-form div {
    padding: 1vh 0;
}

.book-form input[type="checkbox"] {
    width: 1rem;
    height: 1rem;
    display: inline-block;
    padding: auto 0.3vw;
    margin: auto 0.4vw;
}

.book-form input[type="radio"] {
    width: 1rem;
    height: 1rem;
    display: inline-block;
    padding: auto 0.3vw;
    margin: auto 0.4vw;
}

label {
    font-size: 1.2rem;
    display: block;
}

textarea {
    height: 20vh;
}

.book-form button {
    display: block;
    margin: 5vh auto 0 auto;
    padding: 2vh;
    width: 10vw;
    font-size: 1.3rem;
    font-family: inherit;
    background-color: rgb(84, 187, 178);
    color: white;
    border: none;
    cursor: pointer;
}

.form-btns {
    display: flex;
    justify-content: space-around;
}


/* scroll bar */


/* width */

::-webkit-scrollbar {
    width: 10px;
    margin-top: 5rem;
}


/* Track */

::-webkit-scrollbar-track {
    background: #e6e7ff;
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: #78ced1;
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: #38384a;
}
.html {
    margin: 0 auto;
    width: 100%;
}

button {
    margin: 0 1vh;
    width: 8vh;
    padding: 0.75vh 0;
}

.App {
    width: 100%;
}

a {
    text-decoration: none;
    color: black;
}

a:visited {
    text-decoration: none;
    color: black;
}

form {
    width: 95%;
    margin: 0 auto;
}

input:last-child {
    margin-bottom: 3vh;
}
